
.messages-tab {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f5f5f5;
}

.messages-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.message-item {
  display: flex;
  align-items: center;
  padding: 12px;
  background: #fff;
  border-radius: 12px;
  margin-bottom: 8px;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;

  &:hover {
    background: #f8f8f8;
  }
}

.message-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 12px;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
}

.message-info {
  flex: 1;
  min-width: 0;
}

.message-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
}

.message-sender {
  font-size: 15px;
  font-weight: 600;
  color: #333;
}

.message-time {
  font-size: 12px;
  color: #999;
}

.message-preview {
  margin: 0;
  font-size: 13px;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.unread-badge {
  width: 10px;
  height: 10px;
  background: #ff3b30;
  border-radius: 50%;
  margin-left: 8px;
  flex-shrink: 0;
}

.conversation-view {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f5f5f5;
}

.conversation-header {
  display: flex;
  align-items: center;
  padding: 12px;
  background: #fff;
  border-bottom: 1px solid #e0e0e0;
}

.back-button {
  width: 40px;
  height: 40px;
  border: none;
  background: transparent;
  color: #667eea;
  font-size: 18px;
  cursor: pointer;
  margin-right: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s;

  &:hover {
    background: #f5f5f5;
  }
}

.conversation-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 12px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.conversation-name {
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.conversation-messages {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.message-bubble {
  max-width: 75%;
  padding: 10px 14px;
  border-radius: 18px;
  word-wrap: break-word;

  &.sent {
    align-self: flex-end;
    background: #007aff;
    color: #fff;
    border-bottom-right-radius: 4px;
  }

  &.received {
    align-self: flex-start;
    background: #e5e5ea;
    color: #000;
    border-bottom-left-radius: 4px;
  }
}

.message-text {
  margin: 0 0 4px 0;
  font-size: 15px;
  line-height: 1.4;
}

.message-timestamp {
  font-size: 11px;
  opacity: 0.7;
  display: block;
  text-align: right;
}

.conversation-input {
  display: flex;
  align-items: center;
  padding: 12px;
  background: #fff;
  border-top: 1px solid #e0e0e0;
  gap: 8px;

  input {
    flex: 1;
    padding: 10px 16px;
    border: 1px solid #e0e0e0;
    border-radius: 20px;
    font-size: 15px;
    outline: none;
    transition: all 0.2s;

    &:focus {
      border-color: #667eea;
    }
  }

  .send-button {
    width: 40px;
    height: 40px;
    border: none;
    background: #007aff;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;

    &:hover {
      background: #0056b3;
    }

    &:active {
      transform: scale(0.95);
    }
  }
}
